<!-- Tip 拆箱弹窗 -->
<template>
  <el-dialog
    :title="title"
    top="30vh"
    :visible="value"
    :show-close="true"
    width="920px"
    :lock-scroll="false"
    :destroy-on-close="true"
    :append-to-body="true"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-scrollbar class="scroll-dialog">
      <el-descriptions>
        <el-descriptions-item label="物料编码">
          {{ extraInfo.materialCode || '---' }}
        </el-descriptions-item>
        <el-descriptions-item label="物料名称">
          {{ extraInfo.materialName || '---' }}
        </el-descriptions-item>
        <el-descriptions-item label="规格型号">
          {{ extraInfo.materialSpec || '---' }}
        </el-descriptions-item>
        <el-descriptions-item label="数量">
          {{ extraInfo.num || '---' }}
        </el-descriptions-item>
        <el-descriptions-item label="spq">
          {{ extraInfo.materialSpq || '---' }}
        </el-descriptions-item>
        <el-descriptions-item label="计量单位">
          {{ extraInfo.unitName || '---' }}
        </el-descriptions-item>
        <el-descriptions-item label="位置">
          {{ extraInfo.currentPosition || '---' }}
        </el-descriptions-item>
        <el-descriptions-item label="入库日期">
          {{ extraInfo.inWarehouseDate || '---' }}
        </el-descriptions-item>
        <el-descriptions-item label="在库时长">
          {{ extraInfo.inWarehouseDays || '---' }}
        </el-descriptions-item>
        <el-descriptions-item label="条码号">
          {{ extraInfo.barcodeNumber || '---' }}
        </el-descriptions-item>
      </el-descriptions>
      <el-divider />
      <el-form v-if="!isNext" :ref="refName" :model="form" inline>
        <el-form-item prop="splitNum" label="拆出条码号个数">
          <!-- 正整数 默认值为1 -->
          <el-input-number v-model="form.splitNum" :min="1" />
        </el-form-item>
        <el-form-item prop="perBarcodeNum" label="每个条码号中物料数量">
          <!-- 默认值为1 3位小数 -->
          <el-input-number v-model="form.perBarcodeNum" :min="1" :precision="3" />
        </el-form-item>
        <el-button type="primary" @click="generateBarcode">生成条码</el-button>
      </el-form>
      <table-com
        ref="baseTableRef"
        :refresh-key="refreshKey"
        :table-height-type="150"
        :loading="loading"
        :index="true"
        :index-fixed="false"
        :selection="false"
        :multiple="false"
        :selection-fixed="false"
        :table-column-sort="false"
        :table-data="tableData || []"
        :column-list="columnList"
        :show-operate="isNext"
        :operate-fixed="false"
        :table-selection="tableSelection"
        :operation-width="100"
        @selectionChange="selectionChange"
      >
        <template slot-scope="scope">
          <el-button type="primary" @click="singlePrint(scope.row.id)">打印</el-button>
        </template>
      </table-com>
    </el-scrollbar>
    <div slot="footer">
      <el-button v-if="isNext" type="primary" @click="recoveryUnpacking">恢复拆箱</el-button>
      <el-button v-if="isNext" type="primary" @click="batchPrint">全部打印</el-button>
      <!-- 无条码置灰 -->
      <el-button v-if="!isNext" type="primary" :disabled="!tableData.length" @click="save">保存</el-button>
      <el-button @click="beforeClose">取 消</el-button>
    </div>
  </el-dialog>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import printTagWired from '@/mixins/printTagWired'
import { restoreSplitBarcode2Api, barcodePrintBatchApi, splitMultiBarcodeApi, saveSplitBarcodeListApi } from '@/apis/modules/out-bound/outbound-information'

const INIT_FORM = () => ({
  splitNum: 1,
  perBarcodeNum: 1
})

export default {
  name: 'DevanningDialog',
  mixins: [paginationPage, printTagWired],
  props: {
    value: {
      required: true,
      type: Boolean
    },
    title: {
      type: String,
      default: '拆箱'
    },
    extraInfo: {
      type: Object,
      required: true,
      default: () => ({})
    }
  },
  data() {
    return {
      refName: 'DevanningForm',
      form: INIT_FORM(),
      isNext: false, // 是否展示拆箱详情
      refreshKey: 1, // 表格唯一key
      loading: false, // 表格loading
      tableSelection: [], // 表格选中数据
      columnList: [
        {
          module: 'word',
          prop: 'barcodeNumber',
          label: '条码号',
          align: 'center',
          minWidth: 170,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'input',
          prop: 'snNo',
          label: '序列号',
          align: 'center',
          width: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          clearable: true,
          showOverflowTooltip: false,
          disabled: (item) => this.isNext
        },
        {
          module: 'word',
          prop: 'num',
          label: '数量',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitName',
          label: '计量单位',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'minSkuUnitName',
          label: '最小包装单位',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'currentPosition',
          label: '位置',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      tableData: [] // 拆箱后的条码
    }
  },
  watch: {
  },
  dicts: [],
  methods: {
    // 生成条码(拆箱生成多个条码号)
    generateBarcode() {
      const params = {
        id: this.extraInfo.inventoryBookDetailId, // 被拆箱条码号的数据主键
        ...this.form
      }
      splitMultiBarcodeApi(params)
        .then(res => {
          const { code, data } = res
          if (!code) {
            this.tableData = data
          }
        })
        .catch(err => {
          console.log('err: ', err)
        })
    },
    // 恢复拆箱
    recoveryUnpacking() {
      const params = {
        ids: this.tableData.map(c => c.id)
      }
      restoreSplitBarcode2Api(params)
        .then(res => {
          const { code } = res
          if (!code) {
            // 重置物料基础信息
            this.$emit('confirm', this.extraInfo)
            // 重置条码号列表
            this.tableData = []
            // 状态恢复上一步
            this.isNext = false
          }
        })
        .catch(err => {
          console.log('err: ', err)
        })
    },
    // 打印
    singlePrint(id) {
      this.getDataAndPrint(barcodePrintBatchApi, { ids: [id] })
        .then((res) => {
          console.log('res: ', res)
          this.$emit('confirm')
        })
        .catch(err => {
          console.log('err信息', err)
        })
    },
    // 全部打印
    batchPrint() {
      this.getDataAndPrint(barcodePrintBatchApi, { ids: this.tableData.map(c => c.id) })
        .then((res) => {
          console.log('res: ', res)
          this.$emit('confirm')
        })
        .catch(err => {
          console.log('err信息', err)
        })
    },
    // 保存
    save() {
      const params = {
        tempDataList: [...this.tableData]
      }
      saveSplitBarcodeListApi(params)
        .then(res => {
          const { code, data } = res
          if (!code) {
            // 重置物料基础信息
            this.$emit('confirm', this.extraInfo)
            // 保存完需覆盖表格数据，并展示操作栏
            this.tableData = data
            // 进行下一步操作
            this.isNext = true
          }
        })
        .catch(err => {
          console.log('err: ', err)
        })
    },
    // Tip 表格复选框改变回调
    selectionChange(tableSelection) {
      this.tableSelection = [...tableSelection]
    },
    initSelect() {
      this.isNext = false
      this.tableData = []
    },
    beforeClose(done) {
      this.$emit('input', false)
      this.form = INIT_FORM()
      typeof done === 'function' && done()
    }
  }
}
</script>

<style lang="scss" scoped>
.scroll-dialog {
  height: 350px;
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
  overflow-x: hidden;
}
  ::v-deep .el-dialog__body {
  padding-top: 10px;
  padding-bottom: 0;
}
</style>

