<template>
  <el-drawer
    :center="true"
    :append-to-body="true"
    :close-on-press-escape="false"
    :wrapper-closable="false"
    :title="title"
    :visible="value"
    :destroy-on-close="true"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-form ref="form" :model="form" inline :rules="rules" label-width="120px">
      <el-scrollbar class="scroll-drawer">
        <el-form-item label="版本号" prop="version">
          <el-input v-model.trim="form.version" placeholder="请填写" style="width: 178px;" />
        </el-form-item>
      </el-scrollbar>
      <div class="drawer-footer">
        <el-button :loading="loading" type="primary" @click="handleConfirm('form')">{{ loading ? '提交中 ...' : '确 定' }}</el-button>
        <el-button @click="beforeClose">取 消</el-button>
      </div>
    </el-form>
  </el-drawer>
</template>

<script>
import { addListApi as addApi, updateListApi as updateApi } from '@/apis/modules/in-storage/consign'

const INIT_FORM = () => ({
  version: ''
})

export default {
  name: 'EditDialog',
  components: {
  },
  props: {
    value: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    },
    editType: {
      type: String,
      default: 'add'
    },
    dialogForm: {
      type: Object,
      default: () => {
        return { ...INIT_FORM() }
      }
    }
  },
  data() {
    return {
      form: INIT_FORM(),
      loading: false,
      rules: {
        version: [
          { required: true, message: '请填写版本号', trigger: 'blur' }
        ]
      },
      selectInfo: {
        list: [],
        visible: false,
        radio: ''
      }
    }
  },
  watch: {
    dialogForm: {
      handler: function(value) {
        if (this.editType === 'add') {
          this.form = { ...INIT_FORM() }
        } else {
          this.form = { ...value }
        }
      },
      immediate: true
    }
  },
  methods: {
    handleConfirm(formName) {
      this.$refs[formName].validate((valid) => {
        if (!valid) return false
        this.loading = true
        const params = {
          ...this.form,
          type: this.type,
          ...this.editType === 'modify' && { contractId: 0 }
        }
        const api = {
          add: addApi,
          modify: updateApi
        }[this.editType || 'add']
        api(params).then(res => {
          const { code } = res
          if (!code) {
            this.$message({
              message: `操作成功`,
              type: 'success'
            })
            this.$emit('submit')
            this.beforeClose()
          }
          this.loading = false
        }).catch(err => {
          console.log(err)
          this.loading = false
        })
      })
    },
    initSelect() {
    },
    beforeClose(done) {
      this.$refs.form.resetFields()
      this.$emit('input', false)
      this.form = Object.assign({}, INIT_FORM())
      typeof done === 'function' && done()
    }
  }
}
</script>

<style lang='scss' scoped>
::v-deep:focus {
  outline: 0;
}
::v-deep .el-drawer {
  width: 640px;
}
.drawer-footer{
  text-align: right;
}
.scroll-drawer {
  height: calc(100vh - 150px);
  // overflow: auto;
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
  overflow-x: hidden;
}
</style>

