<template>
  <div class="app-container">
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline @submit.native.prevent>
        <el-row>
          <el-form-item label="按位置查询 : " prop="storageId">
            <el-link
              type="primary"
              style="min-width: 178px"
              @click="chooseLocation"
            >
              {{ query.storageId ? query.currentPosition : "请选择位置" }}
            </el-link>
          </el-form-item>
          <el-button v-if="query.storageId" type="danger" @click="clearLocation">清除</el-button>
        </el-row>
        <el-row>
          <el-form-item prop="materialCode">
            <el-input v-model="query.materialCode" clearable placeholder="物料编号" style="width: 178px;" class="filter-item" @keyup.enter.native="searchInit" />
          </el-form-item>
          <el-form-item prop="contractCode">
            <el-input v-model="query.contractCode" clearable placeholder="合同号" style="width: 178px;" class="filter-item" />
          </el-form-item>
          <el-form-item prop="orderCode">
            <el-input v-model="query.orderCode" clearable placeholder="订单号" style="width: 178px;" class="filter-item" />
          </el-form-item>
          <el-form-item prop="barCode">
            <el-input v-model="query.barCode" clearable placeholder="条码号" style="width: 178px;" class="filter-item" @keyup.enter.native="search" />
          </el-form-item>
          <el-form-item prop="batchNo">
            <el-input v-model="query.batchNo" clearable placeholder="批次号" style="width: 178px;" class="filter-item" />
          </el-form-item>
          <el-form-item prop="materialName">
            <el-input v-model="query.materialName" clearable placeholder="物料名称" style="width: 178px;" class="filter-item" />
          </el-form-item>
          <el-form-item prop="status">
            <el-select v-model="query.status" filterable clearable placeholder="条码状态" style="width: 178px;" class="filter-item">
              <el-option
                v-for="item in [{id: '未入库', name: '未入库'},{id: '已入库', name: '已入库'},{id: '已出库', name: '已出库'},]"
                :key="item.id"
                :label="item.name"
                :value="item.id"
              />
            </el-select>
          </el-form-item>
          <!-- <el-form-item prop="startDate">
          <el-date-picker
            v-model="query.startDate"
            class="filter-item"
            type="date"
            style="width: 178px;"
            value-format="yyyy-MM-dd"
            placeholder="开始时间"
            @change="query.endDate = ''"
          />
        </el-form-item>
        <el-form-item prop="endDate">
          <el-date-picker
            v-model="query.endDate"
            class="filter-item"
            type="date"
            style="width: 178px;"
            value-format="yyyy-MM-dd"
            placeholder="结束时间"
            :picker-options="{disabledDate: (time) => time.getTime() < new Date(query.startDate).getTime()}"
          />
        </el-form-item> -->
          <el-form-item prop="materialAssessType">
            <el-select v-model="query.materialAssessType" filterable clearable placeholder="评估类型" style="width: 178px;" class="filter-item">
              <el-option
                v-for="item in dict['wms_material_assess_type']"
                :key="item.id"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-form-item>
          <el-button
            class="filter-item"
            type="success"
            icon="el-icon-search"
            @click="init"
          >搜索</el-button>
          <el-button
            class="filter-item"
            type="warning"
            icon="el-icon-refresh-left"
            @click="resetForm('filterItem')"
          >重置</el-button>
          <el-button
            class="filter-item"
            style="float: none !important;margin-left: 0 !important;"
            type="primary"
            icon="el-icon-plus"
            @click="handleAddBarcode"
          >新增条码</el-button>
          <el-button
            :disabled="!tableSelection.length"
            class="filter-item"
            type="primary"
            @click="handleBatchPrintBarcode"
          >批量打印</el-button>
        </el-row>
      </el-form>
    </div>
    <el-row>
      <table-com
        :key="refreshTableKey"
        :ref="refName"
        :ref-name="tableRef"
        :selection="true"
        :row-key="rowKey"
        :table-selection="tableSelection"
        :loading="loading"
        :table-column-sort="true"
        :form-ident="formIdent"
        :table-data="tableData"
        :column-list="columnList"
        :show-operate="true"
        :operation-width="150"
        :expand-list="expandList"
        :selectable="selectable"
        @selectionChange="selectionChange"
        @refresh="handleRefresh"
        @rowClick="handleRowClick"
      >
        <template slot-scope="scope">
          <el-button type="primary" @click="handlePrint(scope.row)">打印</el-button>
          <el-button type="primary" :disabled="scope.row.isCanSplit === 0" @click="handleDevanning(scope.row)">拆箱</el-button>
        </template>
      </table-com>
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />
    <!-- 新增 -->
    <edit-dialog
      v-model="dialogVisible"
      :dialog-form="dialogForm"
      :edit-type="editType"
      :title="dialogTitle"
      @submit="init"
    />
    <!-- 拆箱 -->
    <devanning-dialog v-model="devanningvisible" :extra-info="devanningForm" @confirm="handleDevanningConfirm" />
    <!-- 交接单(没有生成条码) -->
    <delivery-receipt-dialog v-model="deliveryReceiptVisible" title="交接单（没条码）" :extra-info="{}" @confirm="init" />
    <!-- 库位 -->
    <storage-location v-if="kuweiVisible" v-model="kuweiVisible" @confirm="selectedStorageLocation" />
  </div>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import { getListApi, barcodePrint, batchPrintApi } from '@/apis/modules/warehouse/barcodeManage'
import EditDialog from './EditDialog'
import printTagWired from '@/mixins/printTagWired'
import tableHeadSet from '@/mixins/tableHeadSet'
import DevanningDialog from './Devanning'
import DeliveryReceiptDialog from './DeliveryReceipt'
import StorageLocation from './StorageLocation'
import { mapGetters } from 'vuex'
import scan from '@/mixins/scan.js'

export default {
  // name: 'WarehouseBarcodeManage',
  components: {
    EditDialog,
    DevanningDialog,
    DeliveryReceiptDialog,
    StorageLocation
  },
  dicts: ['wms_material_assess_type'],
  mixins: [paginationPage, printTagWired, tableHeadSet, scan],
  data() {
    return {
      title: this.$route.meta.title,
      query: {
        storageId: '',
        currentPosition: '',
        contractCode: '',
        orderCode: '',
        barCode: '',
        batchNo: '',
        materialCode: '',
        materialName: '',
        // startDate: '',
        // endDate: '',
        materialAssessType: '',
        status: ''
      },
      refName: 'baseTable',
      tableRef: 'multipleTable',
      loading: false,
      dialogForm: {},
      editType: 'add',
      dialogVisible: false,
      columnSets: [
        {
          prop: 'status',
          vHtml: true,
          formatter: (c) => {
            const obj = {
              '已入库': '#67c23a',
              '未入库': '#f56c6c'
            }
            return `<span style="color: ${obj[c.status]}">${c.status}</span>`
          }
        }
      ],
      devanningForm: {}, // 拆箱弹窗表单详情
      devanningvisible: false, // 拆箱弹窗变量
      deliveryReceiptVisible: false, // 交接单弹窗变量
      kuweiVisible: false,
      rowKey: 'id',
      tableSelection: [],
      refreshTableKey: 1
    }
  },
  computed: {
    dialogTitle() {
      if (this.editType === 'add') {
        return `新增`
      } else {
        return `修改`
      }
    },
    ...mapGetters('app', {
      navbarWarehouseCheckedList: 'navbarWarehouseCheckedList'
    })
  },
  watch: {
    navbarWarehouseCheckedList: {
      handler() {
        this.init()
      },
      deep: true
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    init() {
      this.loading = true
      const params = {
        ...this.query,
        page: {
          ...this.pageInfo
        },
        warehouseIds: this.navbarWarehouseCheckedList.map(item => item.id)
      }
      return new Promise((resolve, reject) => {
        getListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { formIdent, formHeadSets, pageData } = data
            this.formIdent = formIdent
            this.columnList = this.initTableHeadMixins(formHeadSets)
            const { list, ...pageInfo } = pageData
            this.initPage(pageInfo)
            this.tableData = list.map(c => ({ ...c, id: `${c.packageNo}${c.barCode}` }))
            // 翻页保存选中记录
            const rowKey = this.rowKey
            const ids = this.tableSelection.map(i => i[rowKey])
            this.tableData.forEach(row => {
              function check(row) {
                const id = row[rowKey]
                return ids.some(i => i === id)
              }
              if (check(row)) {
                this.$nextTick(() => {
                  this.$refs[this.refName].$refs.multipleTable.toggleRowSelection(row, true)
                })
              }
            })
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    // 条码号回车查询
    search() {
      this.handlePublicFn({ barCode: this.query.barCode })
    },
    // 扫码结束
    scanEnd(data) {
      this.handlePublicFn(JSON.parse(data))
    },
    handlePublicFn(obj) {
      const arr = obj.barCode.split(':')
      this.query.barCode = arr.length > 1 ? arr[1] : arr[0]
      this.searchInit()
    },
    handleDevanningConfirm() {
      this.init()
        .then(() => {
        // 选中条码信息同步
          const clickItem = this.tableData.find(c => c.id === this.devanningForm.id)
          clickItem && (this.devanningForm = clickItem)
        })
    },
    // 行内点击
    handleRowClick({ row, column, cell, event }) {
      console.log(row)
      if (column.label === '操作') {
        return false
      }
    },
    // 打印
    handlePrint(item) {
      this.getDataAndPrint(barcodePrint, { barcode: item.barCode || item.packageNo, determine: item.determine })
        .then(res => {
          console.log('res', res)
          this.init()
        })
        .catch(err => {
          console.log('err信息', err)
        })
    },
    // 编辑
    handleEdit(item) {
      this.editType = 'modify'
      this.dialogForm = { ...item }
      this.dialogVisible = true
    },
    // 拆箱
    handleDevanning(item) {
      this.devanningvisible = true
      this.devanningForm = { ...item }
    },
    // 重置查询表单
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.clearSelection()
      this.searchInit()
    },
    // 新增条码
    handleAddBarcode() {
      this.deliveryReceiptVisible = true
    },
    // 选择库位
    chooseLocation() {
      this.kuweiVisible = true
    },
    // 选择库位确认回调函数
    selectedStorageLocation({ currentPosition, storageId }) {
      this.query.currentPosition = currentPosition
      this.query.storageId = storageId
      // 当前按位置查询，需清空之前选中项，防止条码重复打印
      if (this.tableSelection.length) {
        this.tableSelection.forEach(row => {
          this.$nextTick(() => {
            this.$refs[this.refName].$refs.multipleTable.toggleRowSelection(row, false)
          })
        })
        this.tableSelection = []
      }
    },
    // 清除位置信息
    clearLocation() {
      this.query.currentPosition = ''
      this.query.storageId = ''
    },
    handleRefresh() {
      this.clearSelection()
      this.searchInit()
    },
    // 选项改变
    selectionChange(arr) {
      this.tableSelection = [...arr]
    },
    // 多选表格，清空用户的选择
    clearSelection() {
      this.$nextTick(() => {
        this.$refs[this.refName].$refs[this.tableRef].clearSelection()
      })
    },
    // 控制表格某行是否可被勾选
    selectable(row, index) {
      return row.barCode || row.packageNo
    },
    // Tip 获取打印数据并开始打印
    getDataAndPrint(printApi = new Promise(), printParams = {}) {
      if (!printApi) {
        console.log('打印接口不存在')
        return Promise.reject(new Error('error'))
      }
      this.isPrinting = true
      // const loading = this.$loading({
      //   lock: true,
      //   text: 'Loading',
      //   spinner: 'el-icon-loading',
      //   background: 'rgba(0, 0, 0, 0.7)'
      // })
      return new Promise((resolve, reject) => {
        printApi(printParams).then(res => {
          const { code, data } = res
          if (!code) {
            data.forEach(item => {
              this.print(item)
            })
          }
          // loading.close()
          resolve(res)
        }).catch(err => {
          console.log('错误信息', err)
          // loading.close()
          reject(err)
        })
      })
    },
    chunkArray(array, chunkSize) {
      const result = []
      for (let i = 0; i < array.length; i += chunkSize) {
        result.push(array.slice(i, i + chunkSize))
      }
      return result
    },
    // todo
    handleBatchPrintBarcode() {
      const arr = this.tableSelection.map(c => ({ barcode: c.barCode || c.packageNo, determine: c.determine }))
      const len = arr.length
      if (len <= 10) {
        this.getDataAndPrint(batchPrintApi, arr)
      } else {
        const batchedArr = this.chunkArray(arr, 10)
        const promiseArr = batchedArr.map(c => this.getDataAndPrint(batchPrintApi, c))
        const loading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        })
        Promise.all(promiseArr)
          .then(values => {
            console.log('values: ', values)
            loading.close()
          })
          .catch(err => {
            console.log('err: ', err)
            loading.close()
          })
      }
    }
  }
}
</script>

<style lang="scss" scoped>
::v-deep .el-form .el-row{
  margin-bottom: 10px;
}
</style>

