<template>
  <el-dialog
    :title="title"
    top="5vh"
    :visible="value"
    :show-close="true"
    width="60vw"
    :lock-scroll="false"
    :destroy-on-close="true"
    :append-to-body="true"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-scrollbar class="scroll-dialog">
      <el-form ref="filterItem" class="top-filter clearfix" :model="query" inline @submit.native.prevent>
        <el-form-item prop="no">
          <el-input v-model.trim="query.no" placeholder="交接单号" class="filter-item" style="width: 178px;" clearable @keyup.enter.native="init" />
        </el-form-item>
        <el-form-item prop="code">
          <el-input v-model.trim="query.code" placeholder="合同号或订单号" class="filter-item" style="width: 178px;" clearable @keyup.enter.native="init" />
        </el-form-item>
        <el-button
          class="filter-item"
          type="success"
          icon="el-icon-search"
          @click="init"
        >搜索</el-button>
        <el-button
          class="filter-item"
          type="warning"
          icon="el-icon-refresh-left"
          @click="resetForm('filterItem')"
        >重置</el-button>
      </el-form>
      <el-row>
        <table-com
          :ref="refName"
          :table-height-type="490"
          :loading="loading"
          :index="true"
          :index-fixed="false"
          :table-column-sort="false"
          :table-data="tableData"
          :column-list="columnList"
          :operation-width="120"
          @refresh="searchInit"
        >
          <template slot-scope="scope">
            <el-button type="primary" @click="handleGeneratorBarcode(scope.row)">生成条码</el-button>
          </template>
        </table-com>
      </el-row>
      <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />
    </el-scrollbar>
    <div slot="footer">
      <el-button type="primary" @click="handleConfirm">确 定</el-button>
      <el-button @click="beforeClose">取 消</el-button>
    </div>
  </el-dialog>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import { statusChartMap, statusMap } from '@/views/in-storage/purchase/status'
import { getDeliveryReceiptListApi } from '@/apis/modules/warehouse/barcodeManage'

export default {
  name: 'BarcodeManageMaterial',
  components: {
  },
  mixins: [paginationPage],
  props: {
    value: {
      required: true,
      type: Boolean
    },
    title: {
      type: String,
      default: ''
    },
    extraInfo: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      loading: false,
      refName: 'DeliveryReceipt',
      query: {
        no: '',
        code: ''
      },
      tableData: [],
      columnList: [
        {
          module: 'word',
          prop: 'no',
          label: '交接单号',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'contractNo',
          label: '合同编号',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'orderNo',
          label: '订单号',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'supplierName',
          label: '供应商',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'deliveryDate',
          label: '交货日期',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'status',
          prop: 'status',
          label: '状态',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: (status) => { // 按钮文案
            return statusChartMap[status]
          },
          headerAlign: 'center',
          statusFormatter: (row) => { // 按钮类型
            return statusMap[row.status]
          }
        }
      ]
    }
  },
  methods: {
    handleConfirm() {
      this.$emit('confirm')
      this.beforeClose()
    },
    init() {
      const params = {
        ...this.query,
        page: {
          ...this.pageInfo
        }
      }
      return new Promise((resolve, reject) => {
        this.loading = true
        getDeliveryReceiptListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { list, ...pageInfo } = data
            this.initPage(pageInfo)
            this.tableData = list
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    initSelect() {
      this.searchInit()
    },
    handleGeneratorBarcode(item) {
      console.log('item: ', item)
      this.$router.push({
        name: 'InStorageDeliveryModify',
        params: {
          view: 'modify', // 页面类型
          orderId: item.orderId, // 订单id
          id: item.id, // 交接单id
          type: item.type, // 订单类型，1：采购订单，2：外协订单，3：寄售订单，4：成品订单
          routeName: this.$route.name
        },
        query: {
          extraInfo: {
            ...this.$route.params
          }
        }
      })
    },
    beforeClose(done) {
      this.resetForm('filterItem')
      this.$emit('input', false)
      typeof done === 'function' && done()
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    }
  }
}
</script>

<style lang="scss" scoped>
.scroll-dialog {
  height: 600px;
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
  overflow-x: hidden;
}
 ::v-deep .el-dialog__body {
  padding-top: 10px;
  padding-bottom: 0;
}
</style>
