import request from '@/apis/request.wms'

// 主列表
export function getListApi(data = {}) {
  return request({
    url: '/warehouseMgt/wmsDeliveryMaterialDetail/pageList',
    method: 'post',
    data,
    timeout: 2 * 60 * 1000
  })
}

// 拆箱后条码打印
export function barcodePrint(data = {}) {
  return request({
    url: '/enterWarehouse/wmsDeliveryMaterial/unpackingBarcodePrint',
    method: 'post',
    data
  })
}

